import React from "react";
import styled from "styled-components";
import Button from "./Button";
import axios from "axios";

const SearchContainer = styled.div`
  background: white;
  padding: 20px;
  max-width: 700px;
  margin: 0 auto;
  box-shadow: 0 2px 28px 0 rgba(0, 0, 0, 0.7);
`;

const Input = styled.input`
  height: 46px;
  padding: 10px;
  margin: 0;
  float: left;
  width: calc(100% - 145px);
  margin-right: 15px;
  border: 1px solid #ccc;
`;

export default class Search extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentLocation: ""
    };
  }

  componentDidMount() {
    this.getCurrentLocation();
  }

  getCurrentLocation = () => {
    console.log("getCurrentLocation", window.H);

    console.log(navigator.geolocation);

    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(position => {
        console.log(position);

        axios("https://reverse.geocoder.api.here.com/6.2/reversegeocode.json", {
          params: {
            app_id: "28JcRbNy1WcmSmGHHir2",
            app_code: "vDLtLLF6UIqb_gQx13Qo0Q",
            prox: `${position.coords.latitude},${position.coords.longitude}`,
            mode: "retrieveAddresses",
            maxresults: 1
          }
        }).then(response => {
          console.log(
            response,
            response.data.Response.View[0].Result[0].Location.Address.Label
          );

          this.setState({
            currentLocation:
              response.data.Response.View[0].Result[0].Location.Address.Label
          });
        });
      });
    } else {
      console.error("Geolocation is not supported by this browser!");
    }
  };

  render() {
    return (
      <SearchContainer>
        <Input
          type="text"
          placeholder="Address, i.e. Kirchstraße 1"
          value={this.state.currentLocation}
        />
        <Button>Delivery</Button>
      </SearchContainer>
    );
  }
}
